.toggleButton {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: inline-block;
  height: 50px;
  outline: none;
  padding: 0;
  pointer-events: initial;
  position: fixed;
  left: 10px;
  vertical-align: middle;
  width: 50px;
  z-index: 1100; }
  .toggleButton span {
    background-color: #666;
    content: '';
    display: block;
    height: 2px;
    left: calc(50% - 13px);
    position: absolute;
    top: calc(50% - 1px);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transition: background-color 0.2s ease-in-out, top 0.2s 0.2s ease-out, -webkit-transform 0.2s linear;
    transition: background-color 0.2s ease-in-out, top 0.2s 0.2s ease-out, -webkit-transform 0.2s linear;
    transition: background-color 0.2s ease-in-out, top 0.2s 0.2s ease-out, transform 0.2s linear;
    transition: background-color 0.2s ease-in-out, top 0.2s 0.2s ease-out, transform 0.2s linear, -webkit-transform 0.2s linear;
    width: 26px;
    /*     @media screen and(max-width:576px){
    background-color: #fff;
    &:before, &:after{
      background-color: #fff;
    }
  }
   */ }
    .toggleButton span:before, .toggleButton span:after {
      background-color: #666;
      content: '';
      display: block;
      height: 2px;
      position: absolute;
      -webkit-transform-origin: 50% 50%;
              transform-origin: 50% 50%;
      -webkit-transition: background-color 0.2s ease-in-out, top 0.2s 0.2s ease-out, -webkit-transform 0.2s linear;
      transition: background-color 0.2s ease-in-out, top 0.2s 0.2s ease-out, -webkit-transform 0.2s linear;
      transition: background-color 0.2s ease-in-out, top 0.2s 0.2s ease-out, transform 0.2s linear;
      transition: background-color 0.2s ease-in-out, top 0.2s 0.2s ease-out, transform 0.2s linear, -webkit-transform 0.2s linear;
      width: 26px; }
    .toggleButton span:before {
      top: 7px; }
    .toggleButton span:after {
      top: -7px; }
  .toggleButton.active span {
    background-color: transparent;
    -webkit-transition: background 0.2s ease-out;
    transition: background 0.2s ease-out; }
    .toggleButton.active span:before, .toggleButton.active span:after {
      -webkit-transition: top 0.2s ease-out, -webkit-transform 0.2s 0.2s ease-out;
      transition: top 0.2s ease-out, -webkit-transform 0.2s 0.2s ease-out;
      transition: top 0.2s ease-out, transform 0.2s 0.2s ease-out;
      transition: top 0.2s ease-out, transform 0.2s 0.2s ease-out, -webkit-transform 0.2s 0.2s ease-out; }
    .toggleButton.active span:before {
      top: 0;
      -webkit-transform: rotate3d(0, 0, 1, -45deg);
              transform: rotate3d(0, 0, 1, -45deg); }
    .toggleButton.active span:after {
      top: 0;
      -webkit-transform: rotate3d(0, 0, 1, 45deg);
              transform: rotate3d(0, 0, 1, 45deg); }


